.survey-question {
    background: #ffffff;
    width: 80%;
    margin: 10px auto;
    padding: 20px;
    position: relative;
  }
  
  .top-right {
    position: absolute;
    top: 8px;
    right: 16px;
    font-size: 18px;
    cursor: pointer;
  }
  
  .top-right-save {
    position: absolute;
    top: 8px;
    right: 110px;
    font-size: 18px;
    cursor: pointer;
  }
  
  .top-right:hover {
    color: red;
  }
  
  .top-right-save:hover {
    color: green;
  }
  
  .survey-question-input-container {
    margin-bottom: 10px;
    margin-top: 33px;
  }
  
  .survey-question-input {
    width: 100%;
  }
  
  .answer-checkbox {
    margin-right: 5px;
  }
  
  .answer-label {
    margin-left: 5px;
  }
  